import React, { Component } from 'react';

import {
  Switch,
  BrowserRouter as HashRouter,
  Route,
  Link,
} from 'react-router-dom';
import $ from 'jquery';

// import { HashRouter, Route, Link } from 'react-router-dom';

// Contents
import Home from './contents/home';
import Gallary from './contents/gallary';
import NextPopUp from './contents/next-pop-up';
import Members from './contents/members';

// Import images
import HbgMenuPng from './images/hbg-menu.png';

const HbgMenu = {
  backgroundImage: `url(${HbgMenuPng})`,
};

function Main() {
  $(document).ready(function () {
    $('button').click(function () {
      $(this).toggleClass('expanded').siblings('div').slideToggle();
    });
  });

  return (
    // <Router>
    <HashRouter basename='/'>
      {/* Navigator */}
      <nav className='mobile'>
        <button className='toggleButton' style={HbgMenu}>
          Toggle
        </button>
        <div>
          <ul>
            <li>
              <Link to='/'>Home</Link>
            </li>
            <li>
              <Link to='/gallary'>Gallery</Link>
            </li>
            <li>
              <Link to='/nextpopup'>Next Pop Up</Link>
            </li>
            <li>
              <Link to='/members'>Members</Link>
            </li>
          </ul>
        </div>
      </nav>

      <nav className='desktop'>
        <ul>
          <li>
            <Link to='/'>Home</Link>
          </li>
          <li>
            <Link to='/gallary'>Gallery</Link>
          </li>
          <li>
            <Link to='/nextpopup'>Next Pop Up</Link>
          </li>
          <li>
            <Link to='/members'>Members</Link>
          </li>
        </ul>
      </nav>

      {/* Main-Contents */}
      <div className='main'>
        <div className='container'>
          <Switch>
            <Route path='/' exact={true}>
              <Home />
            </Route>
            <Route path='/gallary' exact={true}>
              <Gallary />
            </Route>
            <Route path='/nextpopup' exact={true}>
              <NextPopUp />
            </Route>
            <Route path='/members' exact={true}>
              <Members />
            </Route>
          </Switch>
        </div>
      </div>
      {/* </Router> */}
    </HashRouter>
  );
}

